
import XHeader from 'vux/src/components/x-header/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'


export default {
components: {
XHeader,
FormPreview
},
data () {
let wallets = this.$common.get_wallets()
return {
wallets: wallets,
list: [{
label: '交易账户',
value: 'yyt'
}, {
label: '备注',
value: '交易费'
}]
}
},
methods: {
goDetail (account) {
this.$router.push({path: '/wallet-backup', query: {account: account}})
}
},
mounted () {
}
}



