
import XHeader from 'vux/src/components/x-header/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
XTextarea,
XInput,
XButton,
Msg,
Group,
Alert
},
data () {
return {
tabIndex: 1,
submitting: false,
wifKey: '',
pwd1: '',
error: {
common: ''
},
icon: 'success',
title: '',
buttons: [{
type: 'primary',
text: '',
onClick: this.keepOn.bind(this)
}, {
type: 'default',
text: '',
link: '/'
}]
}
},
watch: {
tabIndex () {
this.error = {
common: ''
}
},
pwd1 () {
this.error = {
common: ''
}
},
wifKey () {
this.error.common = ''
}
},
computed: {
isEqual () {
return this.pwd1
}
},
mounted () {
if (!this.$common.get_disclaimer_accepted()) {






}
},
methods: {
validate () {
this.wifKey = this.wifKey.trim()
if (this.wifKey === '') {
this.error.common = this.$t('index.import_key_isnull')
return false
}
if (!this.isEqual) {
this.error.common = this.$t('wallet_backup.detail.placeholder.password')
return false
}
return true
},
onSubmit () {
if (!this.validate()) {
return
}
this.createByWifKey()
},
createByWifKey () {
setTimeout(() => {
let setData = this.$common.backupImport(this.wifKey, this.pwd1)
this.buttons[1].text = this.$t('wallet_import.success.detail')
if (!setData) {
this.submitting = true
this.icon = 'warn'
this.title = this.$t('wallet_import.error.no_reference_account')
this.buttons[0].type = 'warn'
this.buttons[0].text = this.$t('wallet_import.error.title')
} else {
setData.active = this.$common.encryption(setData.active, this.pwd1)
setData.owner = this.$common.encryption(setData.owner, this.pwd1)
setData.active_pubkey = this.$common.backupPublicKey(setData.active, this.pwd1)
setData.owner_pubkey = this.$common.backupPublicKey(setData.owner, this.pwd1)
setData.backup_date = null
let wallets = this.$common.get_wallets()
if (!this.$common.IsInArray(wallets, setData)) {
wallets.push(setData)
this.$common.set_wallets(wallets)
}
this.submitting = true
this.icon = 'success'
this.title = '操作成功！'
this.buttons[0].type = 'success'
this.buttons[0].text = this.$t('wallet_import.success.title')
}
}, 50)
},
wifKeyTextAreaChange () {
if (this.wifKey === '') {
this.error.common = this.$t('index.import_key_isnull')
return
}
if (this.wifKey.indexOf('(hide)') > -1) {
this.wifKey = this.wifKey.replace('(hide)', '').trim()
}
},
keepOn () {
this.submitting = false
},
focus () {
this.error.common = ''
}
}
}



