
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import util from '../libs/utils'
import accountDetail from '../components/accountDetail'
import AccountImage from '../components/AccountImage'
export default {
name: 'index',
directives: {
TransferDom
},
components: {
Radio,
Group,
Confirm,
Cell,
Alert,
Msg,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet,
AccountImage,
accountDetail
},
methods: {
onClickMore () {
this.showMenu = true
},
changeLocale (locale) {
util.setStore('_locale', locale)
this.$i18n.locale = locale
},
qrcode () {
let _this = this
let permissions = cordova.plugins.permissions
permissions.hasPermission(permissions.CAMERA, checkPermissionCallback, null)
function checkPermissionCallback (status) {
if (!status.hasPermission) {
let errorCallback = function () {
alert('请在系统设置中打开本应用的相机权限')
}
permissions.requestPermission(
permissions.CAMERA,
function (status) {
if (!status.hasPermission) errorCallback()
cordova.plugins.barcodeScanner.scan(
function (result) {


_this.showConfirm = true
_this.code = result

}, null, 'QRCode', 'scan', []
)
},
errorCallback)
} else {
cordova.plugins.barcodeScanner.scan(
function (result) {


_this.showConfirm = true
_this.code = result.text

}, null, 'QRCode', 'scan', []
)
}
}
},
onCancel () {
this.showConfirm = false
},
onConfirm () {
let accountArr = JSON.parse(this.$common.getStore('account'))
let data = ''
let _this = this
accountArr.map(item => {
if (item.account === this.wallets[0].account) {
data = { 'data': item.encryption }
this.$http.post(`/login/qrCode/${this.code}`, data).then(res => {
let responese = res.data
if (responese.code === 200) {
_this.show = true
_this.icon = 'success'
_this.title = this.$t('index.success')
_this.buttons[0].type = 'primary'
_this.buttons[0].text = _this.$t('index.success')
} else {
_this.show = true
_this.icon = 'warn'
_this.title = this.$t('index.error')
_this.buttons[0].type = 'warn'
_this.buttons[0].text = _this.$t('index.error')
}
})
}
})
},
keepOn () {
this.show = false
}
},
mounted () {
this.handler = () => {
if (this.path === '/') {
this.box = document.querySelector('#demo_list_box')
this.updateDemoPosition(this.box.scrollTop)
}
}
},
beforeDestroy () {
this.box && this.box.removeEventListener('scroll', this.handler, false)
},
created () {
this.wallets = this.$common.get_wallets()
console.log(this.$route.path)
},
data () {
return {
showMenu: false,
showConfirm: false,
show: false,
menus: {
'language.noop': '<span class="menu-title">Language</span>',
'zh-CN': '中文',
'en-US': 'English'
},
code: '',
path: '/',
isLoading: false,
direction: 'forward',
drawerVisibility: false,
showMode: 'push',
showModeValue: 'push',
showPlacement: 'left',
wallets: [],
showPlacementValue: 'left',
icon: 'success',
title: this.$t('index.success'),
buttons: [{
type: 'primary',
text: this.$t('index.success'),
onClick: this.keepOn.bind(this)
}]
}
}
}



