
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import AccountImage from '../components/AccountImage'
export default{
props: {
wallet: {
type: Object,
default: {}
}
},
components: {
AccountImage,
Flexbox,
FlexboxItem
},
data () {
return {
balance: '0',
isBackup: true,
isShow: true
}
},
created () {
this.$store.state.wallets.map(item => {
if (item.account === this.wallet.account && item.isBackUp) {
this.isBackup = false
}
})
},
methods: {
linkBackup (account) {
let query = {
account: this.wallet.account
}
this.$router.push({path: '/wallet-backup', query})
},
loadBalance () {
let account = this.wallet.account
this.$http.get(`chain/accounts/eos/${account}`).then(res => {
let data = res.data
if (data.code === 200) {
this.balance = data.data.eos_balance.split(' ')[0]
} else {
this.balance = '0'
}
})
},
show () {
this.isShow = !this.isShow
}
},
mounted () {
this.loadBalance()
}
}



