
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import AccountImage from '../components/AccountImage'
export default{
props: {
wallet: {
type: Object,
default: {}
}
},
components: {
AccountImage,
Flexbox,
FlexboxItem
},
data () {
return {
balance: '0',
isShow: true
}
},
created () {
},
methods: {
show () {
this.isShow = !this.isShow
},
manage () {
let query = {
account: this.wallet.account
}
this.$router.push({path: '/wallet-manage', query})
},
deal () {
let query = {
account: this.wallet.account
}
this.$router.push({path: '/trading-record', query})
}
},
mounted () {
}
}



