
import XHeader from 'vux/src/components/x-header/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import upload from '../components/upload-img'
import { mapState } from 'vuex'
export default {
directives: {
TransferDom
},
data () {
return {
imgs: [],
show: false,
formData: {




entity: 'policy',
type: 'LIHI',
version: '1.0.0',
body: {
file: [] 
}
},
icon: 'success',
title: '操作成功！',
buttons: [{
type: 'primary',
text: '继续上传',
onClick: this.keepOn.bind(this)
}, {
type: 'default',
text: '返回首页',
link: '/'
}]
}
},
computed: {
...mapState({
imgStatus: state => state.img_status,
imgPaths: state => state.img_paths
})
},
methods: {

uploadFile () {

if (this.$store.state.img_upload_cache.length > 0) {
this.$store.state.img_upload_cache.map(item => {
this.formData.body.file.push({
fileHash: item.md5,
fileUrl: item.url
})
})
this.show = true
this.icon = 'success'
this.title = '操作成功！'
setTimeout(() => {
this.$store.commit('set_img_upload_cache', [])
}, 1000)













} else {
this.show = true
this.icon = 'warn'
this.title = '请选择上传的图片！'
}
},
submit () {
let values = []
for (let key of this.imgPaths) {
values.push(key)
}
this.imgs = values
},
confirm () {
this.$store.commit('set_img_upload_cache', [])
},
keepOn () {
this.confirm()
this.show = false
}
},
watch: {
imgStatus () {
if (this.imgStatus === 'finished') {
this.submit()
}
}
},
destoryed () {
this.imgs = []
},
components: {
upload,
Alert,
Msg,
XButton,
XHeader,
Flexbox,
Box,
FlexboxItem
}
}



