
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import Clipboard from 'clipboard'
import PasswordConfirm from '../components/PasswordConfirm.vue'

export default {
components: {
XButton,
XHeader,
Group,
Cell,
Flexbox,
FlexboxItem,
PasswordConfirm,
Confirm
},
data () {
return {
wifKey: '',
show: false,
keyCopied: false,
unlocked: false,
isUnlock: false,
wallet: {},
error: {
common: ''
}
}
},
methods: {
unlock () {
this.error.common = ''
this.isUnlock = true
},
unlocking (pwd) {
let self = this
if (!pwd.trim()) {
this.error.common = this.$t('unlock.error.invalid_password')
return
}
let account = this.$route.query.account
let wallets = this.$common.get_wallets()


let wallet = wallets.find(w => {
return w.account === account
})
let publicWallet = this.$common.backupPublicKey(wallet.active, pwd)
let activePubkey = wallet.active_pubkey
if (wallet == null) {
self.isUnlock = false
self.error.common = self.$t('unlock.account_not_found')
} else if (activePubkey !== publicWallet) {
self.isUnlock = false
self.error.common = self.$t(
'wallet_backup.detail.error.invalid_password'
)
} else {
self.error.common = ''
let data = {
account: wallet.account,
active: wallet.active,
owner: wallet.owner
}
let wifKey = this.$common.backupExport(data, pwd)
self.wifKey = wifKey
self.wallet = wallet
self.unlocked = true
self.isUnlock = false
}
},
copyKey () {
let clipboard = new Clipboard('#copy')
clipboard.on('success', e => {
this.keyCopied = true
let wallets = this.$common.get_wallets()
wallets.map((item, index) => {
if (item.account === this.$route.query.account) {
item.isBackUp = true
this.$store.commit('BACKUPWALLET', index)
}
})
this.$common.set_wallets(wallets)
setTimeout(() => {
this.keyCopied = false
}, 1000)

clipboard.destroy()
})
clipboard.on('error', e => {

console.log('该浏览器不支持自动复制')

clipboard.destroy()
})
},
backup () {
let wallet = this.wallet
wallet.backup_date = new Date().getTime()
this.$common.update_wallet(wallet)
this.$router.replace({
path: '/'
})
},
showPlugin () {
let se = this
if (this.unlocked) {
if (!se.wallet.backup_date) {
this.$vux.confirm.show({
title: this.$t('wallet_backup.detail.confirm'),
content: '',
onConfirm () {
se.backup()
}
})
} else {
se.$router.replace({
path: '/'
})
}
} else {
se.$router.replace({
path: '/'
})
}
},
setUnlock (val) {
this.isUnlock = val
}
},
mounted () {
}
}



